$.ajaxSetup({ cache: false });

angular.module('News.controllers.News', [])

.controller('NewsController', ['$scope', '$routeParams', function($scope, $routeParams){

  $scope.newsId = $routeParams.newsId;
  $scope.newsList = [];

  var setting = _.find($scope.NEWS_SETTING, function(news){return news.id == $scope.newsId;});

  //--------------------------------------

  var _getRss = function(setting, scope){
    var urls = setting.params.urls;
    var needSort = setting.needSort;
    var needRemoveOld = setting.needRemoveOld;

    for(var i = 0, len = urls.length; i < len; i++){
      var url = urls[i];



      url = 'https://query.yahooapis.com/v1/public/yql?q=select%20*%20from%20feed%20where%20url%3D%27'+encodeURIComponent(url)+'%27&format=json&diagnostics=true&callback=';
      $.getJSON(url, function(data){
        if(!data.query.results){
          return;
        }
        //add to scope
        var entries = data.query.results.item;
        for(var i = 0; i < entries.length; i++){
          var entry = entries[i]

          //FIXME: can not parse date string has timezone, such as ICT
          var date = entry.pubDate && entry.pubDate.replace('ICT', '');

          scope.newsList.push({title: entry.title, link: entry.link, date: date});
        }

        //remove too old news
        if(needRemoveOld){
          scope.newsList = _.filter(scope.newsList, function(news){
            return Date.create(news.date).is('today') || Date.create(news.date).is('yesterday');
          });
        }

        //sort
        if(needSort){
          scope.newsList = _.sortBy(scope.newsList, function(news){
            return -Date.create(news.date);
          });
        }

        //distinct
        scope.newsList = _.uniq(scope.newsList, function(news){return news.link;});

        scope.$apply();
      });


    }
  };

  //--------------------------------------

  switch(setting.type){
    case 'rss':
        _getRss(setting, $scope);
        return;
  }

}]);