angular.module('News', [
  'ngRoute',
  'mobile-angular-ui',
  'News.controllers.Main',
  'News.controllers.News',
  'News.controllers.Filter',
])

.config(function($routeProvider) {
  $routeProvider.when('/news/:newsId', {
    controller: 'NewsController',
    templateUrl:'news.html',
    reloadOnSearch: false,
  });
});